import { Component, Output, Input, EventEmitter, Renderer, HostBinding, ElementRef, ViewEncapsulation } from "@angular/core";
import { LoadingService } from "./loading.service";




export class LoadingComponent {
  
  public color: string = '#308BC8';

  
  public height: string = '5';

  /** progress */
  private _progress: number = 0;

  
  public progressChange: EventEmitter<number> = new EventEmitter<number>();

  
  
  public get progress(): number {
    return this._progress;
  }

  public set progress(progress: number) {
    if (this._progress === progress) return;
    this._progress = progress;
    if (this._progress === 100) this.isComplete = true;
    this.progressChange.emit(this._progress);
  }


  /** complete */
  private _isComplete: boolean;

  
  public completeChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  
  public get isComplete(): boolean {
    return this._isComplete;
  }



  public set isComplete(isComplete: boolean) {
    if (this._isComplete === isComplete) return;
    this._isComplete = isComplete;
    if (this._isComplete === true) this.progress = 100;
    this.completeChange.emit(this._isComplete);
  }

  public complete() {
    this.isComplete = true;
  }

  public constructor(public loadingService: LoadingService, public element: ElementRef, public renderer: Renderer) {
  }

  public ngAfterContentInit() {
    if (this.element.nativeElement.id == 'common') {
      this.loadingService.colorChange.subscribe(color => this.renderer.setElementStyle(this.element.nativeElement, "background-color", color));
      this.loadingService.progressChange.subscribe(progress => this.renderer.setElementStyle(this.element.nativeElement, "width", progress + "%"));
      this.loadingService.displayChange.subscribe(display => {
        this.renderer.setElementStyle(this.element.nativeElement, "opacity", display ? "1" : "0")
        this.renderer.setElementStyle(this.element.nativeElement, "filter", display ? "blur(0px)" : "blur(3px)")
      });
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: "ay-loading",
  template: '',
  styles: [`
    ay-loading {
      height: 10px;
      box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
      transition: all .3s linear; }
      ay-loading#common {
        position: fixed;
        left: 0;
        bottom: 0;
        z-index: 1000; }
  `],
  encapsulation: ViewEncapsulation.None
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: LoadingService, },
{type: ElementRef, },
{type: Renderer, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'color': [{ type: HostBinding, args: ['style.backgroundColor', ] },],
'height': [{ type: HostBinding, args: ['style.height.px', ] },],
'progressChange': [{ type: Output },],
'progress': [{ type: Input },{ type: HostBinding, args: ['style.width.%', ] },],
'completeChange': [{ type: Output },],
'isComplete': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
